.about {
    padding-top: 120px;

    .divider {
        margin-left: 0;
    }
    &__inner {
        display: grid;
        grid-template-columns: 400px 670px;
        grid-template-rows: minmax(245px) minmax(255px);
        grid-auto-rows: minmax(245px);
        column-gap: 70px;
        row-gap: 40px;
    }

    &__img {
        grid-row: 1/3;
        border-radius: 5px;
        max-width: 600px;
        img {
          width: 100%;
        }
    }

    &__subtitle {
        position: relative;
        padding-left: 12px;
        padding-top: 1px;
        @include circle;
    }

    &__content {
        padding-top: 8px;
        .divider {
            margin-top: 10px;
        }
    }

    &__title {
        margin-top: 10px;
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 21px;
        margin-top: 20px;
    }
}

.advantages {
    margin-top: 40px;
    &__title {
      position: relative;
      text-align: center;
      @include circle;
      &:after {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    &__inner {
      margin-top: 40px;
      display: grid;
      grid-template: repeat(2, 1fr) / repeat(2, 1fr);
      grid-auto-rows: 1fr;
      column-gap: 30px;
      row-gap: 30px;
    }
    &__item {
        display: grid;
        grid-template-columns: 50px 220px;
        column-gap: 10px;
        background-color: var(--main-color);
        padding: 15px;
        border-radius: 10px;
        &-icon {
            position: relative;
            font-size: 35px;
            display: flex;
            align-items: center;
            justify-content: center;
            max-height: 50px;
            background-color: #fff;
            border-radius: 100%;
            color: #000;
            svg {
              width: 35px;
              height: 35px;
              fill: #000;
            }
        }
        &-title {
          color: #fff;
        }
        &-text {
            margin-top: 5px;
            font-weight: 700;
            font-size: 12px;
            line-height: 18px;
            color: #fff;
        }
    }
}
