.footer {
  padding: 15px 0;
  background-color: #eee;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__policy {
    display: block;
    font-size: 14px;
    color: #000;
    &:hover {
      text-decoration: underline;
    }
  }
  .social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    svg {
      width: 20px;
      height: 20px;
    }
  }
}
